<template>
  <div class="newContractModal">
    <button @click="closeModal" class="close-modal">
      <close-icon fillColor="#2c3e50" class="icon-button" />
    </button>
    <h1>{{ title }}</h1>
    <vue-form
      name="newContractModel"
      :state="formstate"
      @submit.prevent="onSubmit"
      class="newContract__form"
    >
      <div class="newContract__form-container">
        <validate tag="label">
          <div class="newContract__form-row">
            <span>Naam contract*</span>
            <input
              placeholder="Vul een naam in"
              v-model="model.name"
              class="input__styling"
              v-bind:class="{ error: error.name }"
              required
              name="name"
            />
          </div>
          <div class="newContract__form-row">
            <span>Bestand</span>
            <multiselect
              name="files"
              v-model="model.files"
              :options="files"
              ref="multiSelectContacts"
              :multiple="false"
              :close-on-select="false"
              :clear-on-select="false"
              placeholder="Kies contract"
              label="name"
              track-by="name"
              v-bind:class="{ error: error.files }"
            ></multiselect>
          </div>
          <div class="newContract__form-row">
            <span>Categorie</span>
            <multiselect
              name="category"
              v-model="model.category"
              :options="categories"
              ref="multiSelectContacts"
              :multiple="false"
              :close-on-select="false"
              :clear-on-select="false"
              placeholder="Kies categorie"
              label="name"
              track-by="name"
              v-bind:class="{ error: error.category }"
            ></multiselect>
          </div>
          <div class="newContract__form-row">
            <span>Contacten</span>
            <div class="newContract__form-column-alt">
              <multiselect
                name="contacts"
                v-model="model.contacts"
                :options="contacts"
                ref="multiSelectContacts"
                :multiple="true"
                :close-on-select="false"
                :clear-on-select="false"
                placeholder="Kies de contact(en)"
                label="name"
                track-by="name"
                v-bind:class="{ error: error.contacts }"
              >
                <template slot="item" slot-scope="data" v-if="data.length > 0">
                  <span class="multiselect__single">{{ data }}</span>
                </template>
                <template
                  slot="selection"
                  slot-scope="{ values, data, search, isOpen }"
                  v-if="values.length && !isOpen"
                >
                  <span class="multiselect__single"
                    >{{ values.length }} contacten geselecteerd
                  </span>
                </template>
              </multiselect>
              <ul class="resources-list" v-if="model.contacts.length > 0">
                <template v-for="(resource, index) in model.contacts">
                  <li
                    class="resource-item"
                    :data-index="index"
                    :key="resource.id"
                  >
                    <div class="resource-title" :id="index">
                      <span>{{ resource.name }}</span>
                      <span
                        v-on:click.prevent="removeItem(index, model.contacts)"
                        ><close-icon fillColor="#dedede" class="icon-button"
                      /></span>
                    </div>
                  </li>
                </template>
              </ul>
            </div>
          </div>
          <div class="newContract__form-row">
            <span>Notificaties in dagen</span>
            <div class="newContract__form-column-alt">
              <multiselect
                name="daysbeforenotification"
                v-model="model.daysBeforeNotification"
                :options="daysBeforeNotifications"
                :multiple="true"
                label="name"
                :close-on-select="false"
                :clear-on-select="false"
                placeholder="Kies de notificatie dag(en)"
                track-by="name"
                v-bind:class="{ error: error.daysBeforeNotification }"
              >
                <template slot="item" slot-scope="data" v-if="data.length > 0">
                  <span class="multiselect__single">{{ data }}</span>
                </template>
                <template
                  slot="selection"
                  slot-scope="{ values, data, search, isOpen }"
                  v-if="values.length && !isOpen"
                >
                  <span class="multiselect__single"
                    >{{ values.length }} dag(en) geselecteerd
                  </span>
                </template>
              </multiselect>
              <ul
                class="resources-list"
                v-if="model.daysBeforeNotification.length > 0"
              >
                <template
                  v-for="(resource, index) in model.daysBeforeNotification"
                >
                  <li
                    class="resource-item"
                    :data-index="index"
                    :key="resource.id"
                  >
                    <div class="resource-title" :id="index">
                      <span>{{ resource.name }}</span>
                      <span
                        v-on:click.prevent="
                          removeItem(index, model.daysBeforeNotification)
                        "
                        ><close-icon fillColor="#dedede" class="icon-button"
                      /></span>
                    </div>
                  </li>
                </template>
              </ul>
            </div>
          </div>
          <div class="newContract__form-row">
            <span>Bedrijf*</span>
            <button
              @click.prevent="toggleCustomer(true)"
              v-if="!newCustomer"
              class="newContract__newCustomer button__icon"
            >
              <plus-icon fillColor="#2c3e50" class="icon-button" />
            </button>
            <button
              @click.prevent="toggleCustomer(false)"
              v-if="newCustomer"
              class="newContract__existingCustomer button__icon"
            >
              <arrow-left-icon fillColor="#2c3e50" class="icon-button" />
            </button>
            <multiselect
              v-if="!newCustomer"
              name="customers"
              v-model="model.customer"
              :options="customers"
              ref="multiSelectContacts"
              :multiple="false"
              :close-on-select="false"
              :clear-on-select="false"
              placeholder="Kies bedrijf"
              label="name"
              track-by="name"
              v-bind:class="{ error: error.customer }"
            ></multiselect>
            <input
              v-if="newCustomer"
              v-model="model.contractNewCustomer"
              name="contractNewCustomer"
              placeholder="Nieuw bedrijf"
            />
            <button
              class="buttons__button buttons__button--icon"
              @click.prevent="saveNewCustomer"
              v-if="newCustomer"
            >
              Opslaan
            </button>
          </div>
          <div class="newContract__form-row">
            <div class="newContract__form-column">
              <span>vanaf datum contract</span>
              <input
                name="contractDateFrom"
                class="input__styling"
                type="date"
                v-model="model.contractDateFrom"
                required
                v-bind:class="{ error: error.contractDateFrom }"
              />
            </div>
            <div class="newContract__form-column">
              <span>tot datum contract</span>
              <input
                name="contractDateTill"
                type="date"
                class="input__styling"
                v-model="model.contractDateTill"
                v-bind:class="{ error: error.contractDateTill }"
                required
              />
            </div>
          </div>
          <div class="messages" v-if="this.error.messages.length > 0">
            <ul class="errorContainer">
              <li v-for="error in this.error.messages" :key="error">
                {{ error }}
              </li>
            </ul>
          </div>
        </validate>
      </div>
      <div class="buttons__container">
        <button class="buttons__button buttons__button--icon" type="submit">
          Opslaan
        </button>
      </div>
    </vue-form>
  </div>
</template>

<script>
export default {
  props: ["id", "name", "title", "contacten"],
  name: "add-contract",
  components: {},
  data: function() {
    return {
      formstate: {},
      newCustomer: false,
      error: {
        name: false,
        files: false,
        category: false,
        contacts: false,
        daysBeforeNotification: false,
        company: false,
        contractDateFrom: false,
        contractDateTill: false,
        messages: []
      },
      model: {
        name: "",
        category: "",
        customer: "",
        files: "",
        contacts: [],
        contractDateFrom: "",
        contractDateTill: "",
        contractNewCustomer: "",
        daysBeforeNotification: []
      }
    };
  },
  beforeCreate: function() {
    this.$store.dispatch("setCategories");
    this.$store.dispatch("setCustomers");
    this.$store.dispatch("setFiles");
    this.$store.dispatch("setContacts");
    this.$store.dispatch("setMasterData");
    this.$store.dispatch("setDaysBeforeNotification");
  },
  computed: {
    files() {
      return this.$store.getters.getFiles.map(({ fileId, path }) => {
        return { id: fileId, name: path };
      });
    },
    customers() {
      return this.$store.getters.getCustomers.map(({ id, name }) => {
        return { id: id, name: name };
      });
    },
    contacts() {
      return this.$store.getters.getContacts.map(({ id, name }) => {
        return { id: id, name: name };
      });
    },
    daysBeforeNotifications() {
      return this.$store.getters.getDaysBeforeNotification.map(
        ({ id, name }) => {
          return { id: id, name: name };
        }
      );
    },
    categories() {
      return this.$store.getters.getCategories.map(({ id, name }) => {
        return { id: id, name: name };
      });
    },
    masterData() {
      return this.$store.getters.getMasterData;
    }
  },
  methods: {
    onSubmit: function() {
      const checkFields = [
        { name: "name", message: "Naam is niet correct ingevuld" },
        { name: "category", message: "Categorie is niet correct ingevuld" },
        { name: "customer", message: "Bedrijf is niet correct ingevuld" },
        { name: "files", message: "Bestand is niet correct ingevuld" },
        {
          name: "daysBeforeNotification",
          message: "dagen voor notificatie is niet correct ingevuld"
        },
        { name: "contacts", message: "Contact is niet correct ingevuld" },
        {
          name: "contractDateFrom",
          message: "Contractdatum vanaf is niet correct ingevuld"
        },
        {
          name: "contractDateTill",
          message: "Contractdatum tot is niet correct ingevuld"
        }
      ];
      const _this = this;
      this.error.messages = [];
      checkFields.forEach(field => {
        if (!_this.model[field.name]) {
          _this.error.messages.push(field.message);
          _this.error[field.name] = true;
        } else {
          _this.error[field.name] = false;
          if (_this.model[field.name].length === 0) {
            _this.error[field.name] = true;
            _this.error.messages.push(field.message);
          }
        }
      });

      if (
        !this.model.name ||
        (!this.model.category.id && this.model.category.id !== 0) ||
        (!this.model.customer.id && this.model.customer.id !== 0) ||
        (!this.model.files.id && this.model.files.id !== 0) ||
        this.model.contacts.length === 0 ||
        this.model.contractDateFrom.length === 0 ||
        this.model.contractDateTill.length === 0 ||
        this.model.daysBeforeNotification.length === 0
      ) {
        console.log("Fix eerst je form a.u.b");
        return;
      }

      this.$store.dispatch("addContract", {
        name: this.model.name,
        category: this.model.category.id,
        customer: this.model.customer.id,
        file: this.model.files.id,
        contacts: this.model.contacts,
        contractDateFrom: this.model.contractDateFrom,
        contractDateTill: this.model.contractDateTill,
        daysBeforeNotification: this.model.daysBeforeNotification
      });
      // this.$store.dispatch("setContracts");
      this.$emit("close");
    },
    toggleCustomer: function(condition) {
      this.newCustomer = condition;
    },
    saveNewCustomer: function() {
      this.$store.dispatch("addCustomer", {
        name: this.model.contractNewCustomer,
        image: ""
      });
      this.newCustomer = false;
    },
    closeModal: function() {
      this.$emit("close");
    },
    removeItem(index, itemSet) {
      itemSet.splice(index, 1);
    }
  }
};
</script>

<style lang="scss"></style>
